router.beforeEach((to,from,next)=>{
  // console.log( to );
  if( to.matched.some( record => record.meta.auth ) ){
    if( localStorage.getItem('token') ){
      next()
    }else{
      next({
        path : '/login'
      })
    }
  }else{
    next()
  }
})

export default router;
