import axios from 'axios'

let http = axios.create({
  timeout: 20000
})
http.interceptors.request.use(function (config) {
  let url = config.url;
  if(!/^http/.test(url)) {
    config.url = window.globalData.config.domain + config.url;
  }
  // 在发送请求之前做些什么
  //如果有cmd属性说明自己进行了加工,这里就不做处理了.
  if(!config.data.cmd) {
    config.data = {
      cmd: window.globalData.config.cmd,
      body: {
        ...config.data,
        accessToken: window.globalData.config.accessToken
      }
    }
  }
  return config;
});
http.interceptors.response.use(function (response) {
  // console.log(response)
  if(response.data.code != 0) {
    console.log("ajax Error --> " + response.data.body);
    return Promise.reject(new Error( "__" + response.data.code + "__" + response.data.errmsg ));
  }
  return response.data.body;
}, function (error) {
  if (error.response) {
    console.log("ajax Error --> " + error.response.status + " --> " +  error.response.data);
  } else {
    console.log("ajax Error --> " + error.message);
  }
  return Promise.reject(error);
});        

export default http;
